<template>
  <div class="app-page page-video d-flex align-items-center">
    <video src="" id="video" preload="auto" class="w-100" mute playsinline ></video>
    <div class="overlay" v-if="!is_playing"></div>
  </div>
</template>


<style scoped>
  .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    background-color: rgba(0,0,0,0.9);

    z-index: 999;
  }

  video::-webkit-media-controls {
    display: none;
  }

</style>

<script>

import { mapState } from 'vuex';
import { mapGetters } from 'vuex';


const DOWNLOAD_FOLDER = 'connect4';

export default {
  name: 'PageVideo',
  created: function(){
   
    //this.setVideo();
   

  },
  mounted: function(){
    this.local_dir = cordova.file.dataDirectory + DOWNLOAD_FOLDER;
  	this.setVideo();
   

  },
  data () {
    return {
      video_notification: false,
      is_playing: false,
    }
  },
  computed: {
    
   
    ...mapState({
      message: state => state.message,
      
      //local_dir: state => state.local_dir,

    })
  },
  watch: {
  	message: function(new_value){
  		if( this.message.c == 'setVideo' ){
  	   this.is_playing = false;
			 this.setVideo();
  		}
  		else if( this.message.c == 'playVideo' ){	
  			var vid = document.getElementById('video');
        this.is_playing = true;
  			vid.play();
  		}
  		else if( this.message.c == 'stopVideo' ){	
  			var vid = document.getElementById('video');
        this.is_playing = false;
  			vid.pause();
  		}
  	}
  },
  methods: {


  	setVideo: function(){
  		// this.video_path = './media/' + this.message.p.video;

      var self = this;
      var video_src = self.message.p.video;


      window.resolveLocalFileSystemURL(this.local_dir,
          function (fileSystem) {

                
                fileSystem.getFile(self.message.p.video, { create: false, exclusive: false }, function (fileEntry) {


                    var vid = document.getElementById('video');
      
                    vid.addEventListener("canplaythrough canplay loadedmetadata", function() {
                      
                         self.video_notification = true;
                         console.log('Ready to Play');
                         self.$store.commit('setFeedback', {type: 'video_ready', message: 'Video is ready (event)'} );
                      

                    }, false);


                    vid.src = cordova.file.dataDirectory + fileEntry.fullPath;

                    console.log('SRC: ' + vid.src);

                     // LOOP
                    if(self.message.p.loop == 1){
                      vid.setAttribute('loop', true);
                    }
                    else{
                      vid.removeAttribute('loop');
                    }

                    // FAKE PRELOAD
                    if(self.message.p.fakePreload == 1 && self.message.p.play != 1){
                      
                      vid.addEventListener('play', self.handlePlay);

                      const playPromise = vid.play();
                      if (playPromise !== null){
                          playPromise.catch((e) => { console.log(e.message);  })
                      }
                      
                      //self.$store.commit('setFeedback', {type: 'video_ready', message: 'Video is ready (fake preload)'} );
                    }

                    // PLAY ISTANTANEO
                    if(self.message.p.play == 1){
                      vid.play();
                      self.is_playing = true;
                    }
                   




                }, function(err){
                    console.log(err);
                });
            
            
          }, function (err) {

            console.log(err);
          }
          );

      
		  
  	},

    handlePlay: function(){
      console.log( 'handlePlay');
      var vid = document.getElementById('video');
      vid.pause();
      vid.removeEventListener('play', this.handlePlay);
      this.is_playing = false;
    },

    


  }
}
</script>





