<template>
  <div class="app-page page-image">
  <img src="" alt="" id="image">
  </div>
</template>

<script>

import { mapState } from 'vuex';
import { mapGetters } from 'vuex';

const DOWNLOAD_FOLDER = 'connect4';

export default {
  name: 'PageImage',
  mounted: function(){
  	// var self = this;
   //  console.log('Showing ' + self.message.p.image);
  	// if(self.message.p != undefined){
  	// 	self.$store.commit('setFeedback',{type: 'image_set', message: 'Image set: ' + self.message.p.image });
  	// }

    this.local_dir = cordova.file.dataDirectory + DOWNLOAD_FOLDER;
    this.setImage();

  },
  data () {
    return {
      
    }
  },
  computed: {
   
    ...mapState({
      message: state => state.message,

    })

  },
  methods: {
    setImage: function(){
      // this.video_path = './media/' + this.message.p.video;

      var self = this;

      var image_src = self.message.p.image;


      window.resolveLocalFileSystemURL(this.local_dir,
          function (fileSystem) {
                console.log('local_dir ' + self.local_dir);
                console.log('IMG SRC ' + self.message.p.image);
                
                fileSystem.getFile(self.message.p.image, { create: false, exclusive: false }, function (fileEntry) {

                    var img = document.getElementById('image');

                    img.src = cordova.file.dataDirectory + fileEntry.fullPath;

                    console.log('SRC: ' + img.src);


                }, function(err){
                    console.log(err);
                });
            
            
          }, function (err) {

            console.log(err);
          }
          );

      
      
    },

  }
  
}
</script>




